import * as React from 'react';

import { Avatar, Layout, Menu } from 'antd';
import { ClickParam } from 'antd/lib/menu';
import { connect } from 'react-redux';
import { RouteComponentProps, withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import { Dispatch } from 'redux';

import { headerSelection } from 'src/store/action-creators/headerSelection';
import { IApplicationState, IUser } from 'src/store/applicationState';

import '../css/AppHeader.css';

interface IPropsFromState {
  isAuthenticated: boolean,
  currentUser?: IUser,
  selectedHeaderKey?: string,
}

interface IPropsFromDispatch {
  headerSelection: (selectedKey?: string) => void,
}

const { Header } = Layout;

const mapStateToProps = (state: IApplicationState): IPropsFromState => ({
  currentUser: (state.currentUser) ? state.users[state.currentUser] : undefined,
  isAuthenticated: state.isAuthenticated,
  selectedHeaderKey: state.selectedHeaderKey,
});

const mapDispatchToProps = (dispatch: Dispatch): IPropsFromDispatch => ({
  headerSelection: (selectedKey) => dispatch(headerSelection(selectedKey)),
});

class AppHeader extends React.PureComponent<IPropsFromState & IPropsFromDispatch & RouteComponentProps> {
  public render() {
    return (
      <Header>
        {
          this.props.isAuthenticated &&
          <React.Fragment>
            <div>
              <Link className="header-logo" to={'/'} onClick={this.onClickHome}/>
            </div>

            <Menu
              theme="dark"
              mode="horizontal"
              style={{ lineHeight: '64px', float: 'right' }}
              selectedKeys={this.props.selectedHeaderKey ?
                              [this.props.selectedHeaderKey] :
                              undefined
                           }
            >
              <Menu.Item key="1" onClick={this.onClickMap}>
                Map
              </Menu.Item>
              <Menu.Item key="2" onClick={this.onClickProfile}>
                <Avatar size='large'>{this.getInitialFromUserName()}</Avatar>
              </Menu.Item>
            </Menu>
          </React.Fragment>
        }
    </Header>
    )
  }

  private getInitialFromUserName(): string {
    const currentUser = this.props.currentUser;
    if (currentUser) {
      const matches = currentUser.profileName.match(/\b(\w)/g);
      return matches ? matches.join('').toUpperCase() : 'U';
    }

    return 'U';
  }

  private onClickHome = (): void => {
    this.props.headerSelection(undefined);
  }

  private onClickMap = (e: ClickParam): void => {
    this.props.headerSelection(e.key);
    this.goTo('/map');
  }

  private onClickProfile = (e: ClickParam): void => {
    this.props.headerSelection(e.key);
    this.goTo('/profile');
  }

  private goTo = (path: string): void => {
    this.props.history.push(path);
  }
}

export default withRouter(connect<IPropsFromState, IPropsFromDispatch>(mapStateToProps, mapDispatchToProps)(AppHeader));


