import { delay } from 'redux-saga';
import { put, takeLatest } from 'redux-saga/effects';

import { ILogoutAction, logoutFailed, logoutSuccess } from '../action-creators/logout';
import { ActionTypes } from '../actionTypes';

export function* logoutRequestWatcher() {
  yield takeLatest(ActionTypes.LOGOUT_REQUEST, logout);
}

function* logout(action: ILogoutAction) {
  try {
    yield delay(1000);
    yield put(logoutSuccess());
    action.payload.history.push('/');
  } catch (err) {
    console.log('err');
    yield put(logoutFailed(err));
  }
}


