import { IApplicationState, IUser } from '../applicationState';

export const addOrReplaceUser = (state: IApplicationState, user: IUser): void => {
  state.users[user.id] = user;
}

export const setCurrentUser = (state: IApplicationState, user?: IUser): void => {
  if (user) {
    state.currentUser = user.id;
    state.isAuthenticated = true;
  } else {
    state.currentUser = undefined;
    state.isAuthenticated = false;
  }
}

export const setLoading = (state: IApplicationState, loading: boolean): void => {
  state.isLoading = loading;
}

export const setSelectedHeaderKey = (state: IApplicationState, selectedKey?: string): void => {
  state.selectedHeaderKey = selectedKey;
}



