import produce from 'immer';
import { Reducer } from 'redux';

import { IHeaderSelectionAction } from '../action-creators/headerSelection';
import { ILoginSuccessAction } from '../action-creators/login';
import { ActionTypes } from '../actionTypes';
import { IApplicationState, IUser } from '../applicationState';
import * as Utils from '../utils';

const reducer: Reducer<IApplicationState> = (state: IApplicationState, action) => {
  switch (action.type) {
    case ActionTypes.LOGIN_REQUEST:
      return produce(state, (draftState) => {
        Utils.setLoading(draftState, true);
      });
    case ActionTypes.LOGIN_SUCCESS:
      return produce(state, draftState => {
        const user: IUser = (action as ILoginSuccessAction).payload.user;

        Utils.setCurrentUser(draftState, user);
        Utils.addOrReplaceUser(draftState, user);
        Utils.setLoading(draftState, false);
      });
    case ActionTypes.LOGIN_FAILED:
      return produce(state, (draftState) => {
        Utils.setLoading(draftState, true);
      });
    case ActionTypes.UI_HEADER_SELECTION:
      return produce(state, (draftState) => {
        Utils.setSelectedHeaderKey(draftState, (action as IHeaderSelectionAction).payload.selectedKey);
      });
    case ActionTypes.LOGOUT_REQUEST:
      return produce(state, (draftState) => {
        Utils.setLoading(draftState, true);
      });
    case ActionTypes.LOGOUT_SUCCESS:
      return produce(state, draftState => {
        Utils.setCurrentUser(draftState, undefined);
        Utils.setLoading(draftState, false);
        Utils.setSelectedHeaderKey(draftState, undefined);
      });
    case ActionTypes.LOGOUT_FAILED:
      return produce(state, (draftState) => {
        Utils.setLoading(draftState, false);
      });
    default:
      return state;
  }
}

export default reducer;


